import React from "react";
import styled from 'styled-components';
import background from "../images/about/round_background.png";
import logo from "../images/logo/about_logo.png"
import none from "../images/none.png";
import solux from "../images/about/solux_img.png";
import dacos from "../images/about/dacos_img.png"

const AboutContainer = styled.div`

`

const LogoContainer = styled.div`
    height: 92vh;
`

const MainConatiner = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 60px 0px;
`

const RoundBackground = styled.div`
    /* position: absolute;
    bottom: 0;
    left: 0; */
    background-image: url(${background});
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    position: relative;
    width: 100%;
    height: clamp(80%, 1.5vw, 150%);
    border: none;
`

const MainLogo = styled.div`
    background-image: url(${logo});
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    width: 80%;
    height: 100px;
    margin-bottom: 10px;
`

const MainBoldText = styled.div`
    font-weight: 800;
    font-size: clamp(2.5rem, 1.5vw, 2.8rem);
`

const MainLightText = styled.div`
    font-weight: 100;
    font-size: clamp(0.7rem, 1vw, 0.9rem);
`

const InfoContainer = styled.div`
    height: 85vh;
    margin-top: 70vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content: center;
`

const InfoTextContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
`

const InfoBoldText = styled.div`
    font-weight: 800;
    font-size: clamp(1rem, 1.5vw, 1.3rem);
    width: 55vw;
`

const InfoLightText = styled.div`
    font-weight: 100;
    font-size: clamp(0.7rem, 1vw, 0.9rem);
    width: 55vw;
`

const DSTitle = styled.div`
    margin-top: 10vh;
    font-weight: 600;
    font-size: clamp(2rem, 1vw, 2.5rem);
    color: #0057FF;
    width: 55vw;
`

const DSSubtitle = styled.div`
    font-weight: 200;
    font-size: 0.8rem;
    width: 55vw;
    margin-bottom: 10px;
`

const DSContent = styled.div`
    font-weight: 100;
    font-size: clamp(0.7rem, 1vw, 0.9rem);
    width: 55vw;
    margin-top: 10px;
`

const DacosInfoContainer = styled.div`
    height: 70vh;
`

const SoluxInfoContainer = styled.div`
    height: 73vh;
`

const Image = styled.div`
    width: 55vw;
    height: 250px;
    background-image: url(${none});
    background-size: cover;
    background-repeat: no-repeat;
`

const DacosImage = styled.div`
    width: 55vw;
    height: clamp(100px, 1.5vw, 200px);
    background-image: url(${dacos});
    background-size: contain;
    background-repeat: no-repeat;
`

const SoluxImage = styled.div`
    width: 55vw;
    height: clamp(100px, 1.5vw, 200px);
    background-image: url(${solux});
    background-size: contain;
    background-repeat: no-repeat;
`

const ImageContainer = styled.div`
    display: flex;
    width: 55vw;
    height: 300px;
`

const AboutPage = () => {
    return (
        <AboutContainer>
            <LogoContainer>
                <MainConatiner>
                    <MainLogo />
                    <MainBoldText></MainBoldText>
                    <MainLightText>SOOKMYUNG WOMEN'S UNIVERSITY</MainLightText>
                </MainConatiner>

                <RoundBackground/> 
            </LogoContainer>

            <InfoContainer>
                <ImageContainer>
                    <Image />
                </ImageContainer>

                <InfoTextContainer>
                    <InfoBoldText>DACOS X SOLUX </InfoBoldText>
                    <InfoLightText>DACOS X SOLUX              ,         .</InfoLightText>
                </InfoTextContainer>
            </InfoContainer>

            <DacosInfoContainer>
                <InfoTextContainer>
                    <DSTitle>DACOS</DSTitle>
                    <DSSubtitle>  </DSSubtitle>
                </InfoTextContainer>
                
                <InfoTextContainer>
                    <DacosImage />
                    <DSContent>DACOS      , "Data Analysis Club of Sookmyung"  . DACOS  , ,     ,          .</DSContent>
                </InfoTextContainer>
            </DacosInfoContainer>

            <SoluxInfoContainer>
                <InfoTextContainer>
                    <DSTitle>SOLUX</DSTitle>
                    <DSSubtitle>    </DSSubtitle>
                </InfoTextContainer>

                <InfoTextContainer>
                    <SoluxImage />
                    <DSContent>SOLUX    ,       IT   IT   .</DSContent>
                </InfoTextContainer>
            </SoluxInfoContainer>
        </AboutContainer>
        
    );
};

export default AboutPage;
