import { React, useState } from "react";
import styled from 'styled-components';
import theme from '../images/hackathon/theme_background.png';
import Box from '../component/Box';
//import { useMediaQuery } from 'react-responsive';

const HackathonContainer = styled.div`

`

const ThemeContainer = styled.div`
    height: 75vh;
    width: 100%;
    margin-top: 7vh;
    background-image: url(${theme});
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    background-position: top;
    flex-direction: column;
    justify-content: center; /*     */
    align-items: center; /*     */
    text-align: center;
`
const ThemeText = styled.div`
    font-size: 2.5rem;
    font-weight: 600;
    background-color: transparent;
`
const ThemeTitleContainer = styled.div`
    display: flex;
    align-items: baseline; 
    justify-content: center; 
    background-color: transparent;
`

const EvaluationContainer = styled.div`
    height: auto;
    width: 100%;
    margin-top: 10vh;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center; /*     */
    align-items: center; /*     */
    text-align: left;
`

const PrizeContainer = styled.div`
    height: 75vh;
    width: 100%;
    margin-top: 10vh;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center; /*     */
    align-items: center; /*     */
    text-align: left;
`
const TitleContainer = styled.div`
    display: flex;
    align-items: baseline;
    background-color: transparent;
    width: 55vw;
`

const TitleText = styled.div`
    font-size: clamp(1.35rem, 1vw, 1.5rem);
    font-weight: 600;
    background-color: transparent;
`

const GrayTitleText = styled.div`
    font-weight: 600;
    font-size: clamp(0.7rem, 1.5vw, 0.8rem);
    color: #949494;
    background-color: transparent;
    margin-left: 5px;
`

const SubtitleText = styled.div`
    font-size: 1rem;
    font-weight: 100;
    background-color: transparent;
    width: 55vw;
`

const PrizeBoxContainer = styled.div`
    display: flex;
    margin-top: 1vw;
    width: 55vw;
    justify-content: space-between;
`;

const PrizeBox = styled.div`
    width: 30%;
    height: 15vw;
    border: 1px solid #0057FF;
    background-color: ${({ opacity }) => `rgba(0, 87, 255, ${opacity})`};
    display: flex;
    justify-content: center;
    flex-direction: column; 
    align-items: center;
    text-align: center;
    border-radius: 15px; 
`;

const PrizeTitle = styled.div`
    font-weight: 500;
    font-size: clamp(0.8rem, 2vw, 1.2rem); /*  1rem,  1.4rem   */
    background-color: transparent;
`;

const PrizeMoney = styled.div`
    font-weight: 200; 
    font-size: clamp(0.6rem, 2vw, 0.9rem); /*  0.8rem,  1.2rem   */
    margin-top: 0.5rem;
    background-color: transparent;
`;

const ButtonContainer = styled.div`
    display: flex;
    align-items: center; /*    */
    justify-content: flex-start; /*    */
    gap: 1vw;
    margin-top: 1vw;
    margin-bottom: 2.5vw;
    width: 100%;
    max-width: 55vw;
`;

const Button = styled.button`
    padding: 0.6vw 1.5vw;
    font-size: clamp(0.8rem, 1vw, 0.9rem);
    font-weight: 500;
    border-color: #0057FF;
    border-radius: 30px;
    cursor: pointer;
    background-color: ${({ isActive }) => (isActive ? '#0057FF' : 'black')};
    color: ${({ isActive }) => (isActive ? 'white' : 'grey')};
`;

const HackathonPage = () => {

    //const isMobile = useMediaQuery({ query: "(max-width: 768px)" });
    const [isDS, setIsDS] = useState(true);

    function changePart(newPart){
        if (newPart !== isDS) {
            setIsDS(newPart);
        }
    }


    return (
        <HackathonContainer>
            <ThemeContainer>
                <ThemeTitleContainer>
                    <ThemeText></ThemeText>
                    <GrayTitleText>THEME</GrayTitleText>
                </ThemeTitleContainer>
                <SubtitleText> .       .</SubtitleText>  
            </ThemeContainer>
            
            <EvaluationContainer>
                <TitleContainer>
                    <TitleText> </TitleText>
                    <GrayTitleText>EVALUATION CRITERIA</GrayTitleText>              
                </TitleContainer>

                <ButtonContainer>
                    <Button isActive={isDS}  onClick={() => changePart(true)}>
                          (30)
                    </Button>
                    <Button isActive={!isDS}  onClick={() => changePart(false)}>
                         (70)
                    </Button>
                </ButtonContainer>

                {isDS ? (
                    <>
                    <Box
                        title=' (10)'
                        contents={[
                            '  ?'
                        ]}
                    />
                    <Box
                        title=' (10)'
                        contents={[
                            '  ?'
                        ]}
                    />
                    <Box
                        title=' (10)'
                        contents={[
                            '  ?'
                        ]}
                    />
                    
                </>
                    
                ) : (
                    
                    <>
                        <Box
                            title=' (10)'
                            contents={[
                                '  ?'
                            ]}
                        />
                        <Box
                            title=' (10)'
                            contents={[
                                '    ?'
                            ]}
                        />
                        <Box
                            title=''
                            contents={[
                                '  ?',
                                'UIUX  ?',
                                'SW ? (   )',
                                '   ?'
                            ]}
                        />
                        
                    </>
                )}


            </EvaluationContainer>
            
            <PrizeContainer>
                <TitleContainer>
                    <TitleText></TitleText>
                    <GrayTitleText>PRIZE</GrayTitleText>
                </TitleContainer>
                <PrizeBoxContainer>
                    <PrizeBox opacity={0.2}>
                        <PrizeTitle></PrizeTitle>
                        <PrizeMoney>20</PrizeMoney>
                    </PrizeBox>
                    <PrizeBox opacity={0.5}>
                        <PrizeTitle></PrizeTitle>
                        <PrizeMoney>30</PrizeMoney>
                    </PrizeBox>
                    <PrizeBox opacity={1}>
                        <PrizeTitle></PrizeTitle>
                        <PrizeMoney>50</PrizeMoney>
                    </PrizeBox>
                </PrizeBoxContainer>
            </PrizeContainer>
        </HackathonContainer>
    );
};

export default HackathonPage;
