import { React, useState } from "react";
import styled from 'styled-components';
import background from "../images/timeline/process_background.png";
import Button from "../component/Button";
import none from "../images/none.png";
import { useMediaQuery } from 'react-responsive';

const TimelineContainer = styled.div`
    width: 100%;
    background-color: transparent;
`

const InfoContainer = styled.div`
    height: 90vh;
    width: 100%;
    margin-top: 1vh;
    margin-bottom: 6vh;
    background-image: url(${background});
    background-size: cover;
    background-repeat: repeat;
    background-position: top;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: left;
`

const TitleContainer = styled.div`
    display: flex;
    align-items: baseline;
    background-color: transparent;
    width: 55vw;
`

const TitleText = styled.div`
    font-size: clamp(1.4rem, 1vw, 1.6rem);
    font-weight: 600;
    background-color: transparent;
`

const GrayTitleText = styled.div`
    font-weight: 600;
    font-size: clamp(0.6rem, 1vw, 0.8rem);
    color: #949494;
    background-color: transparent;
    margin-left: 5px;
`

const GraySubtitleText = styled.div`
    font-weight: 400;
    font-size: clamp(0.8rem, 1vw, 1rem);
    color: #949494;
    background-color: transparent;
    width: 55vw;
`

const SubtitleText = styled.div`
    font-size: clamp(1rem, 1vw, 1.2rem);
    font-weight: 100;
    background-color: transparent;
    width: 55vw;
`

const PartContainer = styled.div`
    background-color: transparent;
    margin: 40px 0px 20px 0px;
    width: 55vw;
`

const Part = styled.div`
    height: 5vh;
    background-color: transparent;
    display: flex;
    align-items: center;
`

const BlueText = styled.div`
    color: #0057FF;
    width: 7.5rem;
    font-size: 1rem;
    font-weight: 400;
    background-color: transparent;
`

const TextButton = styled.div`
    width: 10rem;
    height: 30px;
    font-size: clamp(1rem, 1.5vw, 1.2rem);
    font-weight: 400;
    background-color: transparent;
    cursor: pointer;
    padding-right: 0.5rem;
`

const WhiteText = styled.div`
    width: 55vw;
    font-size: 0.9rem;
    font-weight: 400;
    background-color: transparent;
`

const ContentText = styled.div`
    width: 55vw;
    margin-top: 30px;
    font-size: clamp(1rem, 1vw, 1.2rem);
    font-weight: 400;
    background-color: transparent;
`

const TimetableIamge = styled.div`
    width: 27.5vw;
    height: 250px;
    background-image: url(${none});
    background-size: cover;
    background-repeat: no-repeat;
`

const TimetableContainer = styled.div`
    display: flex;
    width: 55vw;
`

const TimelinePage = () => {
    const isMobile = useMediaQuery({ query: "(max-width: 768px)" });
    const [isDS, setIsDS] = useState(true);

  const handleClick = (part) => {
    if (part === 'DS' && !isDS) {
      setIsDS(true);
    } else if (part === 'Dev' && isDS) {
      setIsDS(false);
    }
  };

    return (
        <TimelineContainer>
            <InfoContainer>
                <TitleContainer>
                    <TitleText></TitleText>
                    <GrayTitleText>APPLY</GrayTitleText>
                </TitleContainer>
                <SubtitleText>24. 09. 08 (SUN) nn:nn -24. 09. 14 (SAT) nn:nn</SubtitleText>

                { isMobile &&
                    <>
                        <PartContainer>
                            <Part>
                            <TextButton
                                onClick={() => handleClick('DS')}
                                style={{
                                color: isDS ? "#0057FF" : "#767676",
                                borderBottom: isDS ? "1px solid #FFFFFF" : "1px solid #767676"
                                }}
                                disabled={isDS} //      
                            >
                                 
                            </TextButton>
                            <TextButton
                                onClick={() => handleClick('Dev')}
                                style={{
                                color: !isDS ? "#0057FF" : "#767676",
                                borderBottom: !isDS ? "1px solid #FFFFFF" : "1px solid #767676"
                                }}
                                disabled={!isDS} //     
                            >
                                
                            </TextButton>
                            </Part>

                            { isDS && <>
                                <WhiteText style={{ marginTop: "10px"}}>DACOS 2   OB</WhiteText>
                                <GraySubtitleText style={{ marginBottom: "50px"}}>*  ,       </GraySubtitleText>

                                <Button
                                    onClick={() => {
                                        window.open("https://linktr.ee/DxSHackathon") //   
                                    }}
                                    style = {{ height: "35px", width: "100px"}}
                                    >
                                    
                                </Button>
                            </>
                            }

                            { !isDS && <>
                                <WhiteText style={{ marginTop: "10px"}}>SOLUX 20   OB     </WhiteText>
                                <GraySubtitleText style={{ marginBottom: "50px"}}>*  ,       </GraySubtitleText>

                                <Button
                                    onClick={() => {
                                        window.open("https://linktr.ee/DxSHackathon") //   
                                    }}
                                    style = {{ height: "35px", width: "100px"}}
                                    >
                                    
                                </Button>
                            </>
                            }
                        </PartContainer>
                    </>
                }
                
                { !isMobile && 
                    <>
                    <PartContainer>
                        <Part>
                            <BlueText> </BlueText>   
                            <WhiteText>DACOS 2   OB</WhiteText>
                        </Part>
                        
                        <Part>
                            <BlueText></BlueText>
                            <WhiteText>SOLUX 20   OB     </WhiteText>
                        </Part>
                    </PartContainer>
                    

                    <GraySubtitleText style={{ marginBottom: "50px"}}>*  ,       </GraySubtitleText>

                    <Button
                        onClick={() => {
                            window.open("https://solux.tistory.com/")
                        }}
                        style = {{ height: "40px", width: "100px"}}
                        >
                        
                    </Button>
                    </>
                }
                
            </InfoContainer>
            
            <InfoContainer>
                <TitleContainer>
                    <TitleText> </TitleText>
                    <GrayTitleText>TEAM REVEAL</GrayTitleText>
                </TitleContainer>
                
                <SubtitleText>2024. 11. 01 (FRI)</SubtitleText>
                
                <ContentText>  2,  2,  2   . (   )</ContentText>

                <GraySubtitleText>*   </GraySubtitleText>

            </InfoContainer>

            <InfoContainer>
                <TitleContainer>
                    <TitleText></TitleText>
                    <GrayTitleText>PRE - PREPARATION</GrayTitleText>
                </TitleContainer>
                
                <SubtitleText>2024. 11. 01 (FRI) - 24. 11. 07 (THU)</SubtitleText>
                
                <ContentText>            .<br />     ,     .</ContentText>

            </InfoContainer>

            <InfoContainer>
                <TitleContainer>
                    <TitleText>  </TitleText>
                    <GrayTitleText>HACKATHON</GrayTitleText>
                </TitleContainer>
                
                <SubtitleText>2024. 11. 08 (FRI) - 24. 11. 09 (SAT)</SubtitleText>
                
                <ContentText>Time table</ContentText>
                <TimetableContainer>
                    <TimetableIamge />
                    <TimetableIamge />
                </TimetableContainer>

                <ContentText style={{ marginTop: "10px"}}> 2 </ContentText>
                <GraySubtitleText>*,     </GraySubtitleText>

            </InfoContainer>


        </TimelineContainer>
    );
};

export default TimelinePage;
